@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-SemiBold.woff2") format("woff2"), url("../fonts/AlumniSans-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-Bold.woff2") format("woff2"), url("../fonts/AlumniSans-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../fonts/AlumniSans-ExtraBold.woff2") format("woff2"), url("../fonts/AlumniSans-ExtraBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

html {
  scroll-behavior: smooth;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.header {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 26px;
  line-height: 31px;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 120px;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1200px) {
  .header {
    margin-bottom: 62px;
    background: conic-gradient(from 139.4deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}
@media (max-width: 767px) {
  .header {
    margin-bottom: 64px;
    background: conic-gradient(from 171.43deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}

.header__container {
  display: grid;
  grid-template-rows: minmax(44px, auto) 1fr;
  padding-top: 23px;
}
@media (max-width: 1439px) {
  .header__container {
    padding-top: 23px;
  }
}
@media (max-width: 1200px) {
  .header__container {
    gap: 12px;
    padding-top: 26px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 6px;
    gap: 57px;
  }
}

.header__top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1;
}

.header__link {
  display: block;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.header__link:hover {
  color: #030a1a;
}
@media (max-width: 1200px) {
  .header__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0;
  }
}
@media (max-width: 767px) {
  .header__link {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
  }
}

@media (max-width: 1200px) {
  .header__wrapper-link > .header__link {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
    font-weight: 700;
  }
}
@media (max-width: 767px) {
  .header__wrapper-link > .header__link {
    font-weight: 600;
  }
}

.header__description {
  display: grid;
  padding-left: 21px;
  padding-bottom: 51px;
  gap: 0 15px;
  grid-template-columns: repeat(2, max-content) 1fr;
  grid-template-rows: repeat(3, max-content);
}
@media (max-width: 1200px) {
  .header__description {
    grid-template-columns: 1fr 1fr 1fr;
    padding-left: 0;
    padding-bottom: 82px;
    gap: 0 25px;
  }
}
@media (max-width: 790px) {
  .header__description {
    grid-template-columns: repeat(2, max-content) 1fr;
  }
}
@media (max-width: 767px) {
  .header__description {
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 78px;
  }
}

.header__city {
  margin: 0;
  margin-top: 53px;
  margin-right: 49px;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-align: right;
  align-self: end;
  grid-column: 2/3;
  grid-row: 1/2;
  max-width: 300px;
}
@media (max-width: 1200px) {
  .header__city {
    margin-right: 4px;
    margin-top: 27px;
    font-size: 34px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .header__city {
    margin-top: 0;
    margin-right: 3px;
    font-size: 28px;
    line-height: 34px;
  }
}

.header__heading {
  margin: 0;
  margin-top: 13px;
  margin-bottom: 5px;
  font-size: 92px;
  line-height: 110px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-align: right;
  align-self: start;
  grid-column: 2/3;
  grid-row: 2/3;
  max-width: 300px;
  -webkit-text-stroke: 3px #ffffff;
}
@media (max-width: 1200px) {
  .header__heading {
    font-size: 72px;
    line-height: 86px;
    margin-top: 8px;
    margin-bottom: 13px;
    text-transform: none;
    letter-spacing: 3px;
  }
}
@media (max-width: 767px) {
  .header__heading {
    margin-top: 15px;
    margin-bottom: 22px;
    font-size: 72px;
    line-height: 86px;
  }
}

.header__list {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-top: 52px;
  padding: 0;
  text-align: right;
  align-self: center;
  list-style-type: none;
  grid-column: 1/2;
  grid-row: 2/3;
  max-width: 200px;
}
@media (max-width: 1200px) {
  .header__list {
    margin-top: 39px;
  }
}
@media (max-width: 767px) {
  .header__list {
    margin-top: 38px;
    margin-left: -5px;
    margin-right: -33px;
  }
}

.header__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  color: #ffffff;
  max-width: 234px;
  margin-right: -2px;
  margin-left: 15px;
  padding: 20px 0 19px;
  background-color: rgba(255, 255, 255, 0);
  border: 4px solid #ffffff;
  text-transform: uppercase;
  align-self: start;
  letter-spacing: 0.05em;
  grid-column: 2/3;
  grid-row: 3/4;
}
@media (max-width: 1200px) {
  .header__button {
    max-width: 187px;
    min-width: 183px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .header__button {
    max-width: none;
    margin-left: -2px;
    padding-bottom: 21px;
    grid-column: span 2;
  }
}
.header__button:hover, .header__button:focus, .header__button:active {
  color: #030a1a;
  border-color: #030a1a;
}

.header__image {
  max-width: 500px;
  width: 100%;
  grid-column: 3/4;
  grid-row: 1/-1;
  object-fit: cover;
}
@media (max-width: 1200px) {
  .header__image {
    max-width: none;
    height: 390px;
  }
}
@media (max-width: 767px) {
  .header__image {
    display: none;
  }
}

.header__image-container {
  margin-left: 50px;
  margin-top: -10px;
}
@media (max-width: 1200px) {
  .header__image-container {
    margin: 0;
    min-width: 324px;
  }
}

.header__wrapper-link {
  margin-left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.logo {
  margin-top: 10px;
}
@media (max-width: 1200px) {
  .logo {
    margin-top: 9px;
  }
}

@media (max-width: 1200px) {
  .logo__icon {
    width: 83px;
    height: 29px;
  }
}
@media (max-width: 767px) {
  .logo__icon {
    width: 68px;
    height: 25px;
  }
}

